
<template>
  <div class="nav-bar">
    <ul class="nav-list">
      <router-link  class="nav-list-item active" to="home">
        <i class="navicon home"></i>
        <span>数字宇宙</span>
      </router-link>
      <router-link  class="nav-list-item" to="category">
        <i class="navicon privateart"></i>
        <span>行囊</span>
      </router-link>
      <router-link  class="nav-list-item" to="user">
        <i class="navicon user"></i>
        <span>我的</span>
      </router-link>
        <router-link  class="nav-list-item" to="login">
            <i class="navicon login"></i>
            <span>登录</span>
        </router-link>
    </ul>
  </div>
</template>

<script>
import { onMounted, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { getLocal } from '@/common/js/utils'
export default {
  setup() {
    const route = useRoute()
    const store = useStore()
    onMounted(() => {
      const token = getLocal('token')
      const path = route.path
      if (token && !['/home', '/category'].includes(path)) {
        store.dispatch('updateCart')
      }
    })
    const count = computed(() => {
      return store.state.cartCount
    })

    return {
      count
    }
  }
}
</script>
