          {
            title:'虚拟人物',
            year:'',
            num:'9'
          }]

      })
      onMounted(async () => {
       setTimeout(()=>{
          state.isntLogin=false;
         setTimeout(()=>{
           state.categoryNum=1;
         },3000)
       },3000)
        //state.categoryNum=1;

        let itemviewSize = document.documentElement.clientWidth/3.5;
        if(itemviewSize>200){
          itemviewSize=120
        }
        state.itemviewWidth =itemviewSize
        state.itemviewHeight =itemviewSize - 15;
      })

      const onLoad =()=>{
        setTimeout(()=>{
          state.loading=false;
          state.finished = true
        },3000)
      }

      const goToDetail = () => {
        router.push({ path: `/product/${state.goods[state.goodsActive].id}` })
      }
      const onclickimg = (imgviewobj) => {
        state.goodsActive=imgviewobj.index;
      }
      const goToArtistProducts = () => {
        router.push({ path: `/artist-product/${state.goods[state.goodsActive].id}` })
      }
      const goToLogin = () => {
        router.push({ path: `/login` })
      }
      const goToHome = () => {
        router.push({ path: `/home` })
      }

      return {
        ...toRefs(state),
        goToDetail,
        onLoad,
        onclickimg,
        goToArtistProducts,
        goToLogin,
        goToHome
      }
    },
  }
</script>
